import {getHomeNews, getStatus ,getInfo, getExpre, getCouldData} from './../../api/api';

export const getHomeNewsData = () => {
    return dispatch => {getHomeNews().then(res => {
            let list = res.data.data.pc_feed_focus||[];
            return dispatch({
                type:'GETHOMRNEWS',
                homeNewsList:list
            })
        })
    }
};

export const getStatusList = (data) => {
    return dispatch => {getStatus(data).then(res => {
        if(res.data.code===200){
            let list = res.data.data.list||[];
            return dispatch({
                type:'GETSTATUS',
                statuList:list
            })
        }
    })
    }
};

export const getNewsList = (data) => {
    return dispatch => {getStatus(data).then(res => {
        if(res.data.code===200){
            let d = res.data.data;
            return dispatch({
                type:'GETNEWSLIST',
                newsList:d.list||[],
                totalPage:parseInt(d.totalPage),
                curPage:parseInt(d.page)
            })
        }
    })
    }
};

export const getCommList = (data, type) => {
    return dispatch => {getCouldData(data).then(res => {
        if(res.data.code===200){
            let d = res.data.data||[];
            return dispatch({
                type:type?'GETCOMMENT':'LOADMORECOMM',
                commList:d.list,
                totalPage:parseInt(d.totalPage),
                curPage:parseInt(d.page)
            })
        }
    })
    }
};

export const getInfoData = (data)=> {
    return dispatch => {
        getInfo(data).then(res => {
            if(res.data.code===200){
                let list = res.data.data||[];
                return dispatch({
                    type:'GETINFO',
                    myInfo:list
                })
            }
        })
    }
};

export const getExpreData = (data)=> {
    return dispatch => {
        getExpre(data).then(res => {
            if(res.data.code===200){
                let list = res.data.data;
                return dispatch({
                    type:'GETEXPRE',
                    expre:list
                })
            }
        })
    }
};