import React, { Component } from 'react';
import { Link } from 'react-router-dom';
import './sider.css';

export default class Sider extends Component{
    state = {
        navList:[
            {
                title:'个人主页',
                path:'/'
            },
            {
                title:'最近动态',
                path:'/news'
            },
            {
                title:'三言两语',
                path:'/shows'
            },
            {
                title:'关于我',
                path:'/about'
            },
            {
                title:'联系我',
                path:'/touch'
            }

        ],
        isSelect:0,
    };
    changeClass = (i) =>{
        this.setState({isSelect:i});
    };
    render(){
        const list = this.state.navList.map( (item, i) => {
            return(
                <li onClick={() => this.changeClass(i)} className={this.state.isSelect===i?'now':''} key={i}>
                    <Link to={item.path} replace>{item.title}</Link>
                </li>
            )
        });
        return(
            <div className='head-con'>
                <div className='logo'>
                    <span>webSevan.club</span>
                </div>
                <div className='nav'>
                    {list}
                </div>
            </div>
        );
    }
}
