let defaultStore = {
    /**
     * 个人信息
     * */
    myInfo:'',
    expre:[],

};

export const aboutData = (state = defaultStore, action)=>{
    switch (action.type) {
        case 'GETINFO':
            return {...state,...action};
        case 'GETEXPRE':
            return {...state,...action};
        default:
            return state;
    }
};