let defaultStore = {
    newsList:[],
    totalPage:1,
    curPage:1,
};

export const newsData = (state = defaultStore, action) => {
    switch (action.type) {
        case 'GETNEWSLIST':
            return {...state, ...action};
        case 'SEARCHLIST':
            return {...state, ...action};
        default:
            return state;
    }
};