let defaultStore = {
    commList:[],
    totalPage:1,
    curPage:0,
};

export const commData = (state = defaultStore, action) => {
  switch (action.type) {
      case 'GETCOMMENT':
          // action.commList = state.commList.concat(action.commList);
          return {...state, ...action};
      case 'LOADMORECOMM':
          action.commList = state.commList.concat(action.commList);
          return {...state, ...action};
      default:
          return state;
  }
};