import React,{Component} from 'react';
import propTypes from 'prop-types';
import {splicStr} from "../../utils/sevan";
import './statu.css';

export default class StatuCard extends Component{
    static propTypes = {
      statuList:propTypes.array.isRequired
    };
    render() {
        const cardItem =this.props.statuList.map((item, i) => {
            let imgStr = '';
            if(item.imgurl){
                let imgArr = item.imgurl.split('|');
                imgStr = imgArr.map((url,i)=>{
                    return (
                        <img key={i} src={url} alt={item.title} />
                    )
                });
            }
            return (
                <div key={i} className='statu-item'>
                    <div className='statu-img'>
                        {imgStr}
                    </div>
                    <div className='statu-det'>
                        <p className='statu-tit'>
                            <span className='t'>title：</span><span className='v'>{item.title}</span>
                        </p>
                        <p className='statu-time'>
                            <span className='t'>time：</span><span className='v'>{item.ptime}</span>
                        </p>
                        <p className='contxt'>{splicStr(item.ndesc, 45)}</p>
                    </div>
                </div>
            )
        });
        return (
            <div className='card-list'>
                {cardItem}
            </div>
        );
    }

}