let defaultStore = {
    /**
     * 首页数据
     * */
    statuList:[],
    homeNewsList:[],
};

export const homeData = (state = defaultStore, action) => {
    switch (action.type) {
        case 'GETHOMRNEWS':
            return {...state, ...action};
        case 'GETSTATUS':
            return {...state, ...action};
        default:
            return state;
    }
};