import React,{Component} from 'react';
import './home.css'

import Status from './../../components/statu/statu';
import {connect} from 'react-redux';
import propTypes from 'prop-types';
import {getHomeNewsData, getStatusList} from "./../../store/actions/action";
// import Top from "./../../components/top/top";

class ItemHome extends Component{
    static propTypes = {
        getHomeNewsData:propTypes.func.isRequired,
        getStatusList:propTypes.func.isRequired,
        homeData:propTypes.object.isRequired
    };
    state = {};
    componentDidMount(){
        this.props.getHomeNewsData();
        this.props.getStatusList({action:'getStatus'});
    };
    render(){
        const newList = this.props.homeData.homeNewsList.map((item,i) => {
            return(
                <div key={i} className='news-item'>
                    <div className='new-img'>
                        <img src={item.image_url} alt={item.title} />
                    </div>
                    <p className='tit'>{item.title}</p>
                </div>
            )
        });
        return(
            <div className='main-content rig-container'>
                {/*<Top></Top>*/}
                <div className='home-item'>
                    <div className='home-staus'>
                        <Status statuList={this.props.homeData.statuList}/>
                    </div>
                    <div className='home-news'>
                        {newList}
                    </div>
                </div>
            </div>
        )
    }
}

export default connect(state => ({
    homeData:state.homeData
}),{
    getHomeNewsData,
    getStatusList
})(ItemHome);